import styled from "styled-components";
import MusicianCard from "../../components/Musician/MusicianCard";
import SearchIcon from "@mui/icons-material/Search";
import React, { useState, useEffect, useRef } from "react";
import { useInView } from "react-intersection-observer";
import { useSelector, useDispatch } from "react-redux";
import axios from "axios";
import { loadMusicianList, resetMusicianList } from "../../actions";

export function Musician() {
  /* state
  loading:  true  false
  loadAll:    20     true    
  startIdx:  Idx  20 
  keyword:   , search  input   
  initial:   (  )
  */
  const [loading, setLoading] = useState(true);
  const [loadAll, setLoadAll] = useState(false);
  const [startIdx, setStartIdx] = useState(1);
  const [keyword, setKeyword] = useState("");
  const [initial, setInitial] = useState(true);
  //scroll
  const [ref, inView] = useInView();

  //redux
  const musicianList = useSelector((state) => state.musicianReducer);
  const dispatch = useDispatch();
  const keywordInput = useRef();

  // musician  
  const loadMusicians = () => {
    const server =
      process.env.REACT_APP_SERVER_ADDRESS || "http://127.0.0.1:12367";
    const req = `${server}/musician?startIndex=${startIdx}&endIndex=${
      startIdx + 19
    }&keyword=${keyword}`;
    setLoading(true);

    axios.get(req).then((res) => {
      const musicianData = res.data.data;
      dispatch(loadMusicianList(musicianData));
      if (musicianData.length < 20) setLoadAll(true);

      setStartIdx(startIdx + 20);
      setLoading(false);
    });
  };

  const handleInputEnter = (e) => {
    if (e.key === "Enter") {
      search();
    }
  };
  const search = () => {
    setKeyword(keywordInput.current.value);
  };

  useEffect(() => {
    //           load
    //startIdx !== 1   
    if (inView && !loading && !loadAll && startIdx !== 1) {
      loadMusicians();
    }
  }, [inView, loading]);

  useEffect(() => {
    //   list
    dispatch(resetMusicianList());

    //   idx   load
    if (startIdx !== 1) {
      setStartIdx(1);
    } else {
      loadMusicians();
      if (initial) {
        setInitial(false);
      }
    }
  }, [keyword]);

  useEffect(() => {
    //  loadAll true    
    if (startIdx === 1 && loadAll) {
      setLoadAll(false);
      loadMusicians();
    } else if (startIdx === 1 && !initial) {
      loadMusicians();
    }
  }, [startIdx]);

  return (
    <Body>
      <Nav>
        <h1>Find your Musician</h1>
        <SearchWrap>
          <SearchInput
            ref={keywordInput}
            type="text"
            placeholder="   ."
            onKeyPress={handleInputEnter}
          />
          <SearchButton onClick={search}>
            <SearchIcon />
          </SearchButton>
        </SearchWrap>
      </Nav>
      <CardsWrap>
        {musicianList.musicians.map((musician, idx) => {
          return <MusicianCard key={idx + startIdx} musician={musician} />;
        })}
      </CardsWrap>
      <div id="observer" ref={ref}>
        {" "}
      </div>
    </Body>
  );
}

// styled
const Body = styled.div`
  box-sizing: border-box;
  height: 100%;
  font-family: Impact, Charcoal, sans-serif;
`;

const Nav = styled.nav`
  margin: 25px auto;
  height: 80px;
  width: 980px;
  display: flex;
  align-items: center;
  justify-content: space-between;
`;
const SearchWrap = styled.div`
  position: relative;
  display: flex;
`;

const SearchInput = styled.input`
  width: 300px;
  border: 3px solid #848cb5;
  border-right: none;
  padding: 5px;
  height: 20px;
  border-radius: 10px 0 0 10px;
  outline: none;
  color: #6b6b84;
  &:focus {
    color: #3b3b54;
  }
`;
const SearchButton = styled.button`
  width: 40px;
  height: 36px;
  border: 1px solid #848cb5;
  background: #848cb5;
  text-align: center;
  color: #fff;
  border-radius: 0 10px 10px 0;
  cursor: pointer;
  font-size: 20px;
`;

const CardsWrap = styled.section`
  width: 980px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
`;
